.Home {
  width: 100%;
  height: 100vh;
  background-color: #f4f9e2;
}

.HomeNav {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.Logo {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  letter-spacing: -0.035em;
  color: #62d0df;
}

.Navbar {
  display: flex;
  justify-content: space-between;
  width: 227px;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  color: #22356f;
}

.Navbar a {
  text-decoration: none;
  color: #22356f;
}

.Navbar p {
  border-bottom: 1px solid transparent;
  padding: 10px;
}

.Navbar p:hover {
  border-color: black;
  cursor: pointer;
}

.CountactBtn {
  width: 150px;
  height: 50px;
  background: #62d0df;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  z-index: 10;
}

.CountactBtn a {
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
}

.HomeContentText {
  width: 600px;
  height: 176px;
  position: absolute;
  top: 241px;
  left: 179px;
}

.HomeContentTextLogo {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  letter-spacing: -0.035em;
  color: #62d0df;
}

.HomeContentTextUpperText {
  font-family: "Montserrat", sans-serif;
  font-size: 48px;
  letter-spacing: -0.035em;
  color: #22356f;
  text-transform: uppercase;
  font-weight: bold;
}

.HomeContentTextPar {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  color: #22356f;
}

.HomeContentTextBtn {
  display: flex;
  text-transform: uppercase;
}

.HomeContentTextBtn p {
  width: 150px;
  height: 50px;
  font-size: 16px;
  line-height: 23px;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.1em;
  color: #fab63e;
  transition: 500ms;
}

.HomeContentTextBtn p:hover {
  background-color: #fab63e;
  color: #fff;
  cursor: pointer;
}

.HomeContentImage {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
}
